import closeButton from '../images/close-button.svg';
import Button from './Button';

function PopupWithForm(props) {
    return (
        <section className={`popup popup_${props.name} ${props.isOpen && 'popup_opened'}`}>
            <div className="popup__container">
                <button className="popup__close-button" onClick={props.onClose}><img src={closeButton} alt="Закрывающая кнопка" /></button>
                <h3 className="popup__title">{props.title}</h3>
                <form className='popup__form' onSubmit={props.onSubmit}>
                    {props.children}
                    <Button formName={props.formName} btnName={props.btnName}></Button>
                </form>
            </div>
        </section>
    );
}

export default PopupWithForm;