import closeButton from '../images/close-button.svg';

function ImagePopup(props) {

    return (
        <section className={`popup popup_img ${props.isOpen && 'popup_opened'}`}>
            <div className="popup__container popup__container_img">
                <div className="popup__img">
                    <button className="popup__close-button popup__close-button_img" onClick={props.onClose}><img src={closeButton} alt="Закрывающая кнопка" /></button>
                    <img src={props.props.link} alt={props.props.name} className="popup__pic" />
                </div>
                <span className="popup__text">{props.props.name}</span>
            </div>
        </section>
    );
}

export default ImagePopup;