import PopupWithForm from './PopupWithForm';
import {CurrentUserContext} from '../contexts/CurrentUserContext';
import React from 'react';
import Input from './Input';
import Span from './Span';

function EditProfilePopup(props) {

    const currentUser = React.useContext(CurrentUserContext);
    const [name, setName] = React.useState(currentUser.name);
    const [description, setDescription] = React.useState(currentUser.about);

    function handleChangeName(event) {
        setName(event.target.value);
    }

    function handleChangeDescription(event) {
        setDescription(event.target.value);
    }

    function handleSubmit(e) {
        e.preventDefault();
        props.onUpdateUser({
            name: name,
            about: description,
        });
    } 

    React.useEffect(() => {
        setName(currentUser.name);
        setDescription(currentUser.about);
    }, [currentUser]); 

    return (
        <PopupWithForm title='Редактировать' name='edit' isOpen={props.isOpen} onClose={props.onClose} formName='edit' btnName="Ок, я понял :з" onSubmit={handleSubmit}>
            <label className="popup__label">
                <Input type='text' name='name' count='2' placeholder='Ты кто такой?' minLength='2' maxLength='30' onChange={handleChangeName} value={name || ''}/>
                <Span name='name' count='2'/>
            </label>
            <label className="popup__label">
                <Input type='text' name='info' count='2' placeholder='Че кого, су***ы?' minLength='2' maxLength='300' onChange={handleChangeDescription} value={description || ''}/>
                <Span name='info' count='2'/>
            </label>
        </PopupWithForm>
    );
}

export default EditProfilePopup;