import React from "react";
import { Route, Switch, withRouter } from "react-router-dom";
import Header from "./Header";
import Footer from "./Footer";
import Register from "./Register";
import InfoToolTip from "./InfoToolTip";
import Login from "./Login";
import { currentUser, CurrentUserContext } from "../contexts/CurrentUserContext";
import Content from "./Content";
import ProtectedRoute from "./ProtectedRoute";
import iconOK from '../images/iconOK.svg';
import iconError from '../images/iconError.svg';
import * as auth from '../utils/auth';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      currentUser: currentUser,
      loggedIn: false,
      // email: '',
      isOpenToolTip: false,
      toolTipInfo: {}
    }
    this.handleLogin = this.handleLogin.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
    this.handleRegister = this.handleRegister.bind(this);
    this.setCurrentUser = this.setCurrentUser.bind(this);
    this.successRegister = this.successRegister.bind(this);
    this.closeTooltip = this.closeTooltip.bind(this);
  }

  componentDidMount() {
    this.getCurrentUser();
  }

  getCurrentUser() {
    const token = localStorage.getItem('token');
    auth.getUserInfo(token)
      .then((res) => {
        this.setState({ loggedIn: true });
        this.setState({ currentUser: res })
        this.props.history.push('/');
      }).catch((err) => console.log(err));
  }

  //перенести handleRegistar
  handleRegister(email, password) {
    auth.register(email, password)
      .then(() => {
        this.openToolTip('Вы успешно зарегистрировались', iconOK, 'OK');
        this.successRegister();
      })
      .catch(() => {
        this.openToolTip('Что-то пошло не так! Попробуйте ещё раз.', iconError, 'Error');
      });
  }

  successRegister() {
    this.props.history.push('/sign-in');
  }
  //обработать ошибки в handleLogin
  handleLogin(email, password) {
    auth.login(email, password)
      .then(data => {
        localStorage.setItem('token', data.token);
        this.setState({
          loggedIn: true
        });
        this.getCurrentUser();
        this.props.history.push('/');
      })
      .catch(() => {
        this.openToolTip('Что-то пошло не так! Попробуйте ещё раз.', iconError, 'Error');
      });
  }

  handleLogout() {
    localStorage.removeItem('token');
    this.setState({ loggedIn: false, currentUser: {}});
    this.props.history.push('/sign-in');
  }

  setCurrentUser(currentUser) {
    this.setState({ currentUser })
  }

  closeTooltip() {
    this.setState({ isOpenToolTip: false, toolTipInfo: {} });
  }

  openToolTip(text, icon, iconName) {
    this.setState({ isOpenToolTip: true, toolTipInfo: { text, icon, iconName } });
  }

  render() {
    return (
      <CurrentUserContext.Provider value={this.state.currentUser} >
        <Header loggedIn={this.state.loggedIn} email={this.state.currentUser.email} handleLogout={this.handleLogout} />
        <Switch>
          <Route path="/sign-up">
            <Register handleRegister={this.handleRegister} />
          </Route>
          <Route path="/sign-in">
            <Login loggedIn={this.state.loggedIn} handleLogin={this.handleLogin} />
          </Route>
          <ProtectedRoute path="/" loggedIn={this.state.loggedIn} component={Content} setCurrentUser={this.setCurrentUser} />
        </Switch>
        <Footer />
        <InfoToolTip icon={this.state.toolTipInfo.icon} iconName={this.state.toolTipInfo.iconName} text={this.state.toolTipInfo.text} isOpen={this.state.isOpenToolTip} onClose={this.closeTooltip} />
      </CurrentUserContext.Provider >
    );
  }
}

export default withRouter(App);
