export const BASE_URL = 'https://api.wabu-labu-dab-dab.nomoredomains.icu';

function getReply(result) {
    if (!result.ok) {
        return Promise.reject('Server error');
    }
    return result.json();
}

function getHeaders() {
    return {
        "Content-Type": "application/json",
        authorization: `Bearer ${localStorage.getItem('token')}`
    }
}

export const getPersonalInfo = () => {
    return fetch(`${BASE_URL}/users/me`, {
        headers: getHeaders()
    }).then(getReply)
}
export const getInitialCards = () => {
    return fetch(`${BASE_URL}/cards`, {
        headers: getHeaders()
    }).then(getReply)
}

export const createCard = ({ name, link }) => {
    return fetch(`${BASE_URL}/cards`, {
        method: "POST",
        headers: getHeaders(),
        body: JSON.stringify({ name, link })
    }).then(getReply)
}

export const deleteCard = ({ name, id }) => {
    return fetch(`${BASE_URL}/cards/${id}`, {
        method: "DELETE",
        headers: getHeaders(),
        body: JSON.stringify({ name })
    }).then(getReply)
}

export const setPersonalInfo = ({ name, about }) => {
    return fetch(`${BASE_URL}/users/me`, {
        method: "PATCH",
        headers: getHeaders(),
        body: JSON.stringify({ name, about })
    }).then(getReply)
}

export const setAvatarInfo = (link) => {
    return fetch(`${BASE_URL}/users/me/avatar`, {
        method: "PATCH",
        headers: getHeaders(),
        body: JSON.stringify(link)
    }).then(getReply)
}

export const checkLikes = (cardId, like) => {
    return fetch(`${BASE_URL}/cards/${cardId}/likes`, {
        method: like ? "PUT" : "DELETE",
        headers: getHeaders()
    }).then(getReply)
}
