
import React from 'react';
import Card from './Card';
import {CurrentUserContext} from '../contexts/CurrentUserContext';

function Main({onEditProfile, onAddCard, onEditAvatar, onCardClick,  cards}) {

    const currentUser = React.useContext(CurrentUserContext);

    return (
    <main className="content">
        <section className="profile">
            <div className="profile__profile-info">
                <a href='/#' className="profile__avatar-box" onClick={onEditAvatar}>
                    <img src={currentUser.avatar} alt="Аватарка чувака" className="profile__avatar"/>
                </a>
                <div className="profile__text">
                    <div className="profile__name">
                    <h1 className="profile__title">{currentUser.name}</h1>
                    <button type="button" className="profile__edit-button" aria-label="Edit" onClick={onEditProfile}/>
                    </div>
                <p className="profile__subtitle">{currentUser.about}</p>
                </div>
            </div>
            <button type="button" className="profile__add-button" aria-label="Add" onClick={onAddCard}/>
        </section>

        <section className="elements">
        <ul className="elements__list">
            {cards.map((item) => {
                return (<Card key={item._id} props={item}/>)
                })
            }
        </ul>
        </section>
    </main>
    );
}

export default Main;
