function Input({ 
    type, 
    name, 
    count, 
    placeholder, 
    minLength, 
    maxLength, 
    onChange, 
    value}) 
    {
        return <input 
        type={type} 
        className={`popup__input popup__input_${name}`} 
        id={`popup__input_${name}_${count}`} 
        onChange={onChange} 
        placeholder={placeholder} 
        minLength={minLength} 
        maxLength={maxLength} 
        required 
        value={value}/>;
    }

export default Input;