import React from "react";
import Main from "./Main";
import PopupWithForm from "./PopupWithForm";
import ImagePopup from "./ImagePopup";
import EditProfilePopup from "./EditProfilePopup";
import EditAvatarPopup from "./EditAvatarPopup";
import AddPlacePopup from "./AddPlacePopup";
import * as api from "../utils/api";

class Content extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            isEditProfilePopupOpen: false,
            isAddCardPopupOpen: false,
            isEditAvatarPopupOpen: false,
            selectedCard: {},
            isSelectedCardOpen: false,
            cards: [],
        }
        this.setCurrentUser = props.setCurrentUser;
        this.handleEditProfileClick = this.handleEditProfileClick.bind(this);
        this.handleAddCardClick = this.handleAddCardClick.bind(this);
        this.handleEditAvatarClick = this.handleEditAvatarClick.bind(this);
        this.handleCardClick = this.handleCardClick.bind(this);
        this.handleSelectedCardClick = this.handleSelectedCardClick.bind(this);
        this.closeAllPopup = this.closeAllPopup.bind(this);
        this.handleCardLike = this.handleCardLike.bind(this);
        this.handleCardDelete = this.handleCardDelete.bind(this);
        this.handleAddPlace = this.handleAddPlace.bind(this);
        this.handleUpdateUser = this.handleUpdateUser.bind(this);
        this.handleUpdateAvatar = this.handleUpdateAvatar.bind(this);
        this.mapCards = this.mapCards.bind(this);
    }

    componentDidMount() {
        this.getInitialCards();
    }

    handleEditProfileClick() {
        this.setState({ isEditProfilePopupOpen: !this.state.isEditProfilePopupOpen })
    }

    handleAddCardClick() {
        this.setState({ isAddCardPopupOpen: !this.state.isAddCardPopupOpen })
    }

    handleEditAvatarClick() {
        this.setState({ isEditAvatarPopupOpen: !this.state.isEditAvatarPopupOpen })
    }

    handleCardClick(card) {
        this.setState({ selectedCard: card })
    }

    handleSelectedCardClick() {
        this.setState({ isSelectedCardOpen: !this.state.isSelectedCardOpen })
    }

    closeAllPopup() {
        this.setState({
            isAddCardPopupOpen: false,
            isEditAvatarPopupOpen: false,
            selectedCard: {},
            isEditProfilePopupOpen: false,
            selectedCardOpen: false,
        })
    }

    mapCards(item) {
        return {
            name: item.name,
            link: item.link,
            onCardClick: this.handleCardClick,
            onCardLike: this.handleCardLike,
            _id: item._id,
            onCardOpen: this.handleSelectedCardClick,
            owner: item.owner,
            likes: item.likes,
            onCardDelete: this.handleCardDelete,
        };
    }

    handleAddPlace(card) {
        api.createCard(card).then((newCard) => {
            this.setState({ cards: [newCard.data, ...this.state.cards].map(this.mapCards) });
            this.closeAllPopup();
        })
            .catch((err) => console.log(err));
    }

    handleUpdateUser(data) {
        api
            .setPersonalInfo(data)
            .then((res) => {
                this.setCurrentUser(res);
                this.closeAllPopup();
            })
            .catch((err) => console.log(err));
    }

    handleUpdateAvatar(avatar) {
        api.setAvatarInfo({ avatar }).then((res) => {
            this.setCurrentUser(res);
            this.closeAllPopup();
        })
            .catch((err) => console.log(err));
    }

    handleCardLike(card) {
        const isLiked = card.likes.some((i) => i === card.currentUserId);
        api.checkLikes(card._id, !isLiked).then(() => {
            api.getInitialCards().then((res) => {
                const cards = res.map(this.mapCards);
                this.setState({ cards: cards });
            });
        })
            .catch((err) => console.log(err));
    }

    handleCardDelete({ name, _id: id }) {
        api.deleteCard({ name, id }).then(() => {
            console.log('delete', this.state.cards)
            const newCards = this.state.cards.filter((c) => c._id !== id);
            this.setState({ cards: newCards });
        })
            .catch((err) => console.log(err));
    }

    getInitialCards() {
        api
            .getInitialCards()
            .then((res) => {
                const cards1 = res.map(this.mapCards);
                this.setState({ cards: cards1 });
            })
            .catch((err) => console.log(err));
    }

    render() {
        return (
            <div>
                <Main
                    cards={this.state.cards}
                    onEditProfile={this.handleEditProfileClick}
                    onAddCard={this.handleAddCardClick}
                    onEditAvatar={this.handleEditAvatarClick}
                    onCardClick={this.handleCardClick}
                    isOpen={this.handleSelectedCardClick}
                />

                <AddPlacePopup
                    isOpen={this.state.isAddCardPopupOpen}
                    onClose={this.closeAllPopup}
                    onAddPlace={this.handleAddPlace}
                />
                <EditProfilePopup
                    isOpen={this.state.isEditProfilePopupOpen}
                    onClose={this.closeAllPopup}
                    onUpdateUser={this.handleUpdateUser}
                />
                <EditAvatarPopup
                    isOpen={this.state.isEditAvatarPopupOpen}
                    onClose={this.closeAllPopup}
                    onUpdateAvatar={this.handleUpdateAvatar}
                />

                <PopupWithForm
                    title="Ты бессердечный! Точно удаляем?"
                    name="confirm"
                    formname="confirm"
                    btnname="Дэээ"
                ></PopupWithForm>

                <ImagePopup
                    isOpen={Object.keys(this.state.selectedCard).length}
                    props={this.state.selectedCard}
                    onClose={this.closeAllPopup}
                ></ImagePopup>
            </div>
        );
    }
}

export default Content;
