import React from 'react';
import { CurrentUserContext} from '../contexts/CurrentUserContext';

function Card({props}) {
    const currentUser = React.useContext(CurrentUserContext);
    const isOwn = props.owner === currentUser._id;
    const cardDeleteButtonClassName = (`element__trash-button ${isOwn ? 'element__trash-button_visible' : 'element__trash-button_hidden'}`); 
    const isLiked = props.likes.some(i => i === currentUser._id);
    const cardLikeIconClassName = (`element__like-icon ${isLiked ? 'element__like-icon_active' : 'element__like-icon_hidden'}`);

    return (
        <li className="element">
            <div className="element__container" >
                <img src={props.link} alt={props.name} className="element__photo" onClick={() => props.onCardClick(props)}/>
            </div>
            <button className={cardDeleteButtonClassName} onClick={() => props.onCardDelete(props)}></button>
            <div className="element__info">
                <span className="element__title">{props.name}</span>
                <div className="element__like-button" onClick={() => props.onCardLike({...props, currentUserId: currentUser._id})}>
                    <button className={cardLikeIconClassName}></button>
                    <span className="element__like-counter">{props.likes.length}</span>
                </div>
            </div>
        </li>
    )
}

export default Card;