import PopupWithForm from './PopupWithForm';
import React from 'react';
import Span from './Span';

function EditAvatarPopup(props) {

    const inputAvatar = React.useRef(null);

    function handleSubmit(e) {
        e.preventDefault();
        props.onUpdateAvatar(inputAvatar.current.value);
        inputAvatar.current.value = '';
    } 

    return (

        <PopupWithForm title='Обновить аватар' name='avatar' isOpen={props.isOpen} onClose={props.onClose} formName='avatar' btnName="Сменить фотокарточку" onSubmit={handleSubmit}>
            <label className="popup__label">
                <input type='url' className='popup__input popup__input_avatar' name='info' count='1' placeholder='Че снова выпендриваешься с аффками?' ref={inputAvatar} src={props.link} />
                <Span name='info' count='1'/>
            </label>
        </PopupWithForm>
    );
}

export default EditAvatarPopup;