import PopupWithForm from './PopupWithForm';
import React from 'react';
import Input from './Input';
import Span from './Span';

function AddPlacePopup(props) {
    const [name, setName] = React.useState('');
    const [link, setLink] = React.useState('');

    function onNameInput ({target: {value}}) {
        setName(value)
    }

    function onLinkInput ({target: {value}}) {
        setLink(value)
    }

    function handleSubmit(e) {
        e.preventDefault();
        props.onAddPlace({
            name,
            link
        });
    } 

    return (
    <PopupWithForm title='Добавить карточку' name="add" isOpen={props.isOpen} onClose={props.onClose} formName='add' btnName="Погнали!" onSubmit={handleSubmit}>
        <label className="popup__label">
            <Input type='text' name='name' count='1' placeholder='Че за проект?' minLength='2' maxLength='30' onChange={onNameInput} value={name}/>
            <Span name='name' count='1'/>
        </label>
        <label className="popup__label">
            <Input type='url' name='info' count='1' placeholder='Ссылочку подкинь, бро!' onChange={onLinkInput} value={link}/>
            <Span name='info' count='1'/>
        </label>
    </PopupWithForm>
    )
}

export default AddPlacePopup;