import closeButton from '../images/close-button.svg';

function infoToolTip (props) {

    return (
        <section className={`popup popup_img ${props.isOpen && 'popup_opened'}`}>
            <div className="popup__container popup__container_message">
                <div className="popup__message">
                    <button className="popup__close-button popup__close-button_img" onClick={props.onClose}><img src={closeButton} alt="Закрывающая кнопка" /></button>
                    <img src={props.icon} alt={props.iconName} className="popup__icon" />
                    <p className="popup__message-text">{props.text}</p>
                </div>
            </div>
        </section>
    );
}

export default infoToolTip;