import {  Link } from "react-router-dom";

function Form(props) { 
    return(
        <div className="form">
            <h3 className="form__title">{props.title}</h3>
            <form className='form__container' onSubmit={props.onSubmit}>
                <div className='form__inputs'>
                    <input type='email' value={props.email || ''} name='email' className='form__input' placeholder='Какой там емаил у тебя для спама?' minLength='2' maxLength='30' onChange={props.onHandleChangeEmail}/>
                    <input type='password' value={props.password || ''} name="password" className='form__input' placeholder='Рекомендую qwerty123' minLength='8' maxLength='30' onChange={props.onHandleChangePassword}/>
                </div>
                <div className="form__buttons">
                    <button className='form__button'>{props.btnName}</button>
                    <Link className="form__link" to={props.href}>{props.link}</Link>
                </div>
            </form>
        </div>
    )
}

export default Form;